'use client';

import { useState } from 'react';
import { TextField, Box, Typography } from '@mui/material';
import { Search } from '@mui/icons-material';
import { colors } from '@/theme';
import type { Charity } from 'lib/charities';
import CharityCard from './CharityCard';
import text from '@/content/text.json';

interface CharitiesListProps {
  charities: Charity[];
}

const CharitiesList: React.FC<CharitiesListProps> = ({ charities }) => {
  const [filter, setFilter] = useState('');

  const filtered = charities.filter(
    c =>
      c.title.toLowerCase().includes(filter.toLowerCase()) ||
      c.category.toLowerCase().includes(filter.toLowerCase()) ||
      c.description.toLowerCase().includes(filter.toLowerCase()),
  );
  //
  return (
    <>
      <Greeting />
      <Box
        sx={{
          p: { xs: 2, sm: 3, md: 4 },
          backgroundColor: colors.secondary,
          // borderRadius: '0 0 32px 32px',
        }}
      >
        <TextField
          label=""
          placeholder={text.home.search}
          variant="filled"
          fullWidth
          value={filter}
          onChange={e => setFilter(e.target.value)}
          slotProps={{
            input: {
              startAdornment: (
                <Search
                  fontSize="small"
                  className="me-1"
                  sx={{ color: colors.secondaryTextLight }}
                />
              ),
            },
          }}
          sx={{
            mb: 3,
            backgroundColor: colors.secondaryLight,
            borderRadius: 2,
            '& .MuiFilledInput-root': {
              backgroundColor: colors.secondaryLight,
              borderRadius: 2.5,
              padding: '10px 12px',
              '&:before, &:after': { display: 'none' },
              '&:hover': { backgroundColor: colors.secondaryLight },
            },
            '& .MuiFilledInput-input': {
              padding: 0,
            },
            '& .MuiInputBase-input::placeholder': {
              color: colors.secondaryTextLight,
              opacity: 1,
            },
          }}
        />
      </Box>
      <Box sx={{ p: { xs: 2, sm: 3, md: 4 } }}>
        {/* <Typography variant="body2" sx={{ mb: 2, color: 'text.secondary' }}>
        {filtered.length} {filtered.length === 1 ? 'charity' : 'charities'} found
      </Typography> */}
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            gap: 2,
          }}
        >
          {filtered.map(charity => (
            <CharityCard key={charity.id} charity={charity} />
          ))}
        </Box>

        {filtered.length === 0 && (
          <Box sx={{ textAlign: 'center', py: 8 }}>
            <Typography variant="h6" color="text.secondary">
              No charities found
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              Try adjusting your search
            </Typography>
          </Box>
        )}
      </Box>
    </>
  );
};

export default CharitiesList;
